    width: clamp(42px, 7.04vh, 76px);
    height: clamp(42px, 7.04vh, 76px);
    /* width: 7.04vh; */
    /* min-width: 42px; */
    /* height: 7.04vh;  */
    /* min-height: 42px; */
    background: 0% 0% no-repeat padding-box;
    /* border-radius: 38px; */
    border-radius: 50%;
    display: inline-block;
    cursor: pointer;
    background-color: #FFFFFF;
  }

  .slideshowDot.active {
    background-color: #E94E1B;
  }

